import { useState } from 'react';
import { Grid, Paper, Box, Typography, Stack, IconButton, Menu, MenuItem } from "@material-ui/core";
import { getTempC, getTempF } from "../App";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import pressure from "../images/pressure.svg";
import humidity from "../images/humidity.svg";
import { Confirm } from "notiflix";

function Locations({ appState, appDispatcher }) {
    const [openOptions, setOpenOptions] = useState(null);
    const confirmDeleteLocation = (dispatcher, index) => {
        setOpenOptions(null);
        Confirm.show(
            "❌ Confirm Deletion",
            "Are you Sure you want to delete this location?",
            "Yes",
            "Cancel",
            () => dispatcher({ type: 'deleteForecast', data: index }),
            () => { },
            { titleColor: '#e66146', fontFamily: "quicksand", useGoogleFont: false }
        )
    }
    return (
        <Stack width='100%' spacing={2} sx={{ backgroundColor: 'blu', px: '0.5rem', overflowX:'auto' }}>
            <Typography variant='h5'
                sx={{
                    backgroundColor: 'black', fontWeight: 'bold', color: 'primary.light',
                    width: 'fit-content', p: '0.3rem', borderRadius: '0.5rem', border: '0.2rem solid', borderColor: 'warning.light'
                }}>Dashboard</Typography>
            <Grid container spacing={1} flexWrap='nowrap'  maxWidth='100vw' sx={{ overflowX: 'auto' }}>
                {appState.locationList.map((item, index) =>
                    <Grid item sx={{ width: 'fit-content', mb: '1rem' }}>
                        <Paper elevation={3} sx={{ p: '0.6rem', background: 'black', color: 'white', border: '0.13rem solid white', borderRadius: '0.7rem' }}>
                            {/* header */}
                            <Grid container columnSpacing={2}>
                                <Grid item width='fit-content' flexGrow='1' xs>
                                    <Stack>
                                        <Typography sx={{ color: 'error.light' }}>{item.current.date}</Typography>
                                        <Typography variant='h6' sx={{}}>{item.city}</Typography>
                                    </Stack>
                                </Grid>

                                <Grid item width='fit-content' alignSelf='center' flexGrow='0' xs>
                                    <Stack direction='row' spacing={0.5} justifyContent='between'>
                                        <Typography sx={{ color: 'warning.light', fontWeight: '500', fontSize: '2.5rem' }}>
                                            {appState.degUnit === 'C' ? getTempC(item.current.temp) : getTempF(item.current.temp)}°{appState.degUnit}
                                        </Typography>
                                        <IconButton id="long-button" onClick={(e) => setOpenOptions(e.currentTarget)} sx={{ color: 'white', alignSelf: 'start' }}>
                                            <MoreVertIcon className='text-white hover:text-green-600' />
                                        </IconButton>
                                        <Menu anchorEl={openOptions} open={Boolean(openOptions)} onClose={() => setOpenOptions(null)}>
                                            <MenuItem onClick={() => {setOpenOptions(null) }}>Select Location</MenuItem>
                                            <MenuItem onClick={() => { setOpenOptions(null) }}>Update Location</MenuItem>
                                            <MenuItem onClick={() => { confirmDeleteLocation(appDispatcher, index); setOpenOptions(null)}}>
                                                Delete location
                                            </MenuItem>
                                        </Menu>
                                    </Stack>
                                </Grid>
                            </Grid>

                            {/* body */}
                            <Grid container direction='row' justifyContent='between' width='100%' sx={{ backgroundColor: 'warning.mai' }}>
                                {/* weather Status */}
                                <Grid item flexGrow='0.1' sx={{mt:'-0.6rem'}}>
                                    <Stack alignItems='center'>
                                        <Box component='img' width='6rem' src={item.current.icon}  />
                                        <Typography sx={{ mt: '-1.5rem', fontSize: '1.3rem', fontWeight: '500', color: 'warning.light' }}>{item.current.weatherStatus}</Typography>
                                    </Stack>
                                </Grid>

                                {/* timezone status */}
                                <Grid item flexGrow='1' xs>
                                    <Stack alignItems='center'>
                                        <Typography sx={{ fontWeight: '500', color: 'primary.light' }}>TimeZone</Typography>
                                        <Typography sx={{ fontWeight: '400' }}>{item.current.timezone}</Typography>
                                    </Stack>
                                    <Stack direction='row' spacing={5} justifyContent='center'>
                                        <Stack >
                                            <Typography sx={{ color: 'primary.light', fontWeight: '500' }}>Sunrise</Typography>
                                            <Typography sx={{ fontWeight: '400' }}>{item.current.sunrise}</Typography>
                                        </Stack>
                                        <Stack sx={{}}>
                                            <Typography sx={{ color: 'primary.light', fontWeight: '500' }}>Sunrise</Typography>
                                            <Typography sx={{ fontWeight: '400' }}>{item.current.sunrise}</Typography>
                                        </Stack>
                                    </Stack>
                                </Grid>
                            </Grid>

                            {/* footer  */}
                            <Grid item xs sx={{ backgroundColor: 're', mt: '0.5rem' }}>
                                <Stack direction='row' width='20rem' spacing={2} sx={{ flexWrap: 'wrap', justifyContent: 'center' }}>
                                    <Stack spacing='0.3rem' direction='row' alignItems='center'>
                                        <Box component='img' width='1.4rem' src={pressure} alt="" ></Box>
                                        <Typography >Pressure: <span style={{ color: 'yellow' }}>{item.current.pressure} hPa</span></Typography>
                                    </Stack>
                                    <Stack spacing='0.2rem' direction='row' alignItems='center'>
                                        <Box component='img' width='1.6rem' src={humidity} alt=""></Box>
                                        <Typography >Humidity: <span style={{ color: 'yellow' }}>{item.current.humidity}%</span></Typography>
                                    </Stack>
                                    <Stack spacing='-0.2rem' direction='row' alignItems='center'>
                                        <Box component='i' width='1.6rem' className='fas fa-wind' />
                                        <Typography >WindSpeed: <span style={{ color: 'yellow' }}>{item.current.windSpeed} m/s</span></Typography>
                                    </Stack>
                                    <Stack spacing='-0.2rem' direction='row' alignItems='center'>
                                        <Box component='i' width='1.6rem' className='fas fa-smog' />
                                        <Typography >Visibility: <span style={{ color: 'yellow' }}>{item.current.visibility} m</span></Typography>
                                    </Stack>

                                </Stack>
                            </Grid>
                        </Paper>
                    </Grid>
                )}
            </Grid>
        </Stack>
    )
};

export default Locations;