import { useState } from 'react';
import { Grid, Paper, Box, Typography, Stack, IconButton, Menu, MenuItem } from "@material-ui/core";
import { getTempC, getTempF } from "../App";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import pressure from "../images/pressure.svg";
import humidity from "../images/humidity.svg";
import { Confirm } from "notiflix";

function Forecast({ appState, appDispatcher }) {
    const [openOptions, setOpenOptions] = useState(null);
    const confirmDeleteLocation = (dispatcher, index) => {
        setOpenOptions(null);
        Confirm.show(
            "❌ Confirm Deletion",
            "Are you Sure you want to delete this location?",
            "Yes",
            "Cancel",
            () => dispatcher({ type: 'deleteForecast', data: index }),
            () => { },
            { titleColor: '#e66146', fontFamily: "quicksand", useGoogleFont: false }
        )
    }
    return (
        <Stack spacing={2}  sx={{ backgroundColor: 'blu', px: '0.5rem', maxWidth:'100vw' }} >
            <Typography variant='h5'
                sx={{
                    backgroundColor: 'black', fontWeight: 'bold', color: 'primary.light', width:'fit-content',
                    p: '0.3rem', borderRadius: '0.5rem', border: '0.2rem solid', borderColor: 'warning.light', textAlign:'center'
                }}>
                8-Day Forecast For {appState.locationList[appState.selectedForecast].city}
            </Typography>
            <Grid container flexWrap='wrap'  spacing={1} >
                {(appState.locationList[appState.selectedForecast].daily || []).map((item, index) =>
                    <Grid item sx={{ width: 'fit-content', mb: '1rem' }}>
                        <Paper elevation={3} sx={{ p: '0.6rem', background: 'black', color: 'white', border: '0.13rem solid white', borderRadius: '0.7rem' }}>
                            {/* header */}
                            <Grid container columnSpacing={2}>
                                <Grid item width='fit-content' flexGrow='1' mb='1rem' xs>
                                    <Stack>
                                        <Typography sx={{ color: 'error.light' }}>{item.date}</Typography>
                                        <Typography variant='h5' sx={{ml:'1rem', fontWeight:'500'}}>{item.day}</Typography>
                                    </Stack>
                                </Grid>

                                <Grid item width='fit-content' alignSelf='center' flexGrow='0' xs>
                                    <Stack direction='row' spacing={0.5} justifyContent='between'>
                                        <Typography sx={{ color: 'warning.light', fontWeight: '500', fontSize: '2.5rem' }}>
                                            {appState.degUnit === 'C' ? getTempC(item.dayTemp) : getTempF(item.dayTemp)}°{appState.degUnit}
                                        </Typography>
                                        <IconButton id="long-button" sx={{ color: 'white', alignSelf: 'start', visibility:'hidden' }}>
                                            <MoreVertIcon className='text-white hover:text-green-600' />
                                        </IconButton>
                                       
                                    </Stack>
                                </Grid>
                            </Grid>

                            {/* body */}
                            <Grid container direction='row' justifyContent='between' width='100%' sx={{ backgroundColor: 'warning.mai' }}>
                                {/* weather Status */}
                                <Grid item flexGrow='0.1' sx={{mt:'-1.9rem'}}>
                                    <Stack alignItems='center'>
                                        <Box component='img' width='6rem' src={item.icon} sx={{ mt: '0' }} />
                                        <Typography sx={{ mt: '-1.5rem', fontSize: '1.3rem', fontWeight: '500', color: 'warning.light' }}>{item.weatherStatus}</Typography>
                                    </Stack>
                                </Grid>

                                {/* timezone status */}
                                <Grid item  flexGrow='1' alignSelf='center' xs>
                                     <Stack item direction='row' justifyContent='space-around'>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Min: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Min: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                    </Stack>

                                    <Stack item direction='row' justifyContent='space-around'>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Day: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Night: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                    </Stack>

                                    <Stack item direction='row' justifyContent='space-around'>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Morn: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                        <Typography>
                                            <Box component='span' sx={{ color: 'primary.light' }}>Eve: </Box>
                                            <Box component='span' sx={{ color: 'yellow' }}>
                                                {appState.degUnit === 'C' ? getTempC(item.minTemp) : getTempF(item.minTemp)}°{appState.degUnit}
                                            </Box>
                                        </Typography>
                                    </Stack>
                                    {/*
                                    <Stack direction='row' spacing={5} justifyContent='center'>
                                        <Stack >
                                            <Typography sx={{ color: 'primary.light', fontWeight: '500' }}>Sunrise</Typography>
                                            <Typography sx={{ fontWeight: '400' }}>{item.current.sunrise}</Typography>
                                        </Stack>
                                        <Stack sx={{}}>
                                            <Typography sx={{ color: 'primary.light', fontWeight: '500' }}>Sunrise</Typography>
                                            <Typography sx={{ fontWeight: '400' }}>{item.current.sunrise}</Typography>
                                        </Stack>
                                    </Stack> */}
                                </Grid>
                            </Grid>

                            {/* footer  */}
                            <Grid item xs sx={{ backgroundColor: 're', mt: '0.5rem' }}>
                                <Stack direction='row' width='20rem' spacing={2} sx={{ flexWrap: 'wrap', justifyContent: 'center' }}>
                                    <Stack spacing='0.3rem' direction='row' alignItems='center'>
                                        <Box component='img' width='1.4rem' src={pressure} alt="" ></Box>
                                        <Typography >Pressure: <span style={{ color: 'yellow' }}>{item.pressure} hPa</span></Typography>
                                    </Stack>
                                    <Stack spacing='0.2rem' direction='row' alignItems='center'>
                                        <Box component='img' width='1.6rem' src={humidity} alt=""></Box>
                                        <Typography >Humidity: <span style={{ color: 'yellow' }}>{item.humidity}%</span></Typography>
                                    </Stack>
                                    <Stack spacing='-0.2rem' direction='row' alignItems='center'>
                                        <Box component='i' width='1.6rem' className='fas fa-wind' />
                                        <Typography >WindSpeed: <span style={{ color: 'yellow' }}>{item.windSpeed} m/s</span></Typography>
                                    </Stack>
                        
                                </Stack>
                            </Grid>
                        </Paper>
                    </Grid>
                )}
            </Grid>
        </Stack>
    )
};

export default Forecast;